export default {
  watch: {
    '$route': function () {
      this.listRefresh()
    }
  },
  mounted () {
    this.listRefresh()
  },
  methods: {
    listQuery (data, option = {}) {
      let {type = 'push'} = option
      let prevQuery = this.$route.query
      let query = Object.assign({}, prevQuery, data)
      this.$router[type]({path: this.$route.path, query})
    },
    listRefresh () {
      if (this.getList) {
        this.getList(Object.assign({}, this.$route.query))
      }
    }
  }
}



