import request from 'utils/request'
import { convertData } from 'utils/tool'

// 获取文件夹列表
export function getFolderList (data) {
  convertData(data, {
    'content_type': 'contentType',
    'type': 'screenType'
  })
  return request({
    method: 'post',
    url: '/screen/web/folder/getFoldersInfoNews',
    data
  })
}

// 添加新的文件夹
export function addNewFolder (data) {
  convertData(data, {
    'content_type': 'contentType',
    'type': 'screenType',
    'machine_ids': 'machineIds',
    'unlimited_time': 'unlimitedTime'
  })
  data['pushIs'] = 0
  return request({
    method: 'post',
    url: '/screen/web/folder/saveFolder',
    data
  })
}

