/**
 * 检测手机号码
 * phoneNumber
 * return Boolean
 * */
export const isPhone = (phoneNumber) => {
  const phoneRegExp = /^1[0-9]{10}$/
  // test是RegExp的实例方法
  return phoneRegExp.test(phoneNumber)
}
/**
 * 检测是否为空字符串
 * str
 * return Boolean
 * */
export const isNullString = (str) => {
  return !String(str).trim()
}
/**
 * 检测是否邮箱
 * str
 * return Boolean
 * */
export const isEmail = (str) => {
  let pattern = /^([a-zA-Z0-9]+[_|_|.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|_|.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/
  return pattern.test(str)
}
/**
 * 检测是否为身份证号
 * ID
 * return Boolean
 * */
export const isIdentifyID = (ID) => {
  const IDRegExp = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
  return IDRegExp.test(ID)
}
/**
 * 检测用户名是否为2-16位的中文或字母
 * ID
 * return Boolean
 * */
export const isUsername = (value) => {
  const usernameExp = /^[a-zA-Z\u4e00-\u9fa5]{2,16}$/
  return usernameExp.test(value)
}
export default {
  isPhone,
  isNullString,
  isEmail,
  isIdentifyID,
  isUsername
}



